import YouziCenter, { MiniGame_Plat_Type } from "./youziscript/YouziCenter";
import { Vector2 } from "xengine/utils/math";
import { EventDispatcher } from "xengine/events/EventDispatcher";
import { ADUnit } from "modules/commerce/ADManager";
import YouziMoreGame from "./youziscript/YouziMoreGame";
import { Youzi_MoreGameUI } from "./youziscript/UI";
import YouziSlideWindow from "./youziscript/YouziSlideWindow";
import YouziMainPush from "./youziscript/YouziMainPush";

export default class YouziProvider extends EventDispatcher {

	private youzi: YouziCenter = null;
	appid: string = '';

	bottom_banner_ad: YouziBottomBannerAdUnit = null;
	fullscreen_ad: YouziFullScreenAdUnit = null;
	more_game_ad: YouziFloatMoreGameAdUnit = null;
	slide_panel_ad: YouziSlideAdPanelADUnit = null;
	main_push_ad: FloatMainPushAdUnit = null;

	constructor(appid: string) {
		super();
		this.appid = appid;
	}

	initialize(): Promise<any> {
		return new Promise((resolve, reject) => {
			Laya.AtlasInfoManager.enable("assets/youzi/fileconfig.json", Laya.Handler.create(null, ()=>{
				Laya.loader.load([
					'assets/youzi/youziTexture.png',
					'assets/youzi/youziTexture.atlas',
					"assets/youzi/Youzi_GuessLike.scene",
					"assets/youzi/Youzi_OffLine.scene",
					"assets/youzi/Youzi_SmallWall.scene",
					"assets/youzi/Youzi_BottomBanner.scene",
					"assets/youzi/Youzi_GuessLikeH.scene",
					"assets/youzi/Youzi_OffLineH.scene",
					"assets/youzi/Youzi_SmallWallH.scene",
					"assets/youzi/Youzi_FullScreen.scene",
					"assets/youzi/Youzi_MainPush.scene",
					"assets/youzi/Youzi_ScreenPage.scene",
					"assets/youzi/Youzi_FullScreenH.scene",
					"assets/youzi/Youzi_MoreGame.scene",
					"assets/youzi/Youzi_SlideWindow.scene",
					"assets/youzi/Youzi_GameBannerView.scene",
					"assets/youzi/Youzi_MoreGameH.scene",
					"assets/youzi/Youzi_SlideWindowH.scene",
				], Laya.Handler.create(null, ()=>{
					this.youzi = YouziCenter.getInstance();
					// YouziCenter.getInstance().youziDebug(true);
					YouziCenter.getInstance().initYouzi(this.appid,'1.00.00', MiniGame_Plat_Type.OppoMiniGame);
					console.log("SDK", "");

					this.bottom_banner_ad = new YouziBottomBannerAdUnit();
					this.fullscreen_ad = new YouziFullScreenAdUnit();
					this.more_game_ad = new YouziFloatMoreGameAdUnit();
					this.slide_panel_ad = new YouziSlideAdPanelADUnit();
					this.main_push_ad = new FloatMainPushAdUnit();
					resolve();
				}));
			}));
		});
	}
}

export class YouziBottomBannerAdUnit extends ADUnit {

	private native_ad: Laya.View = null;

	/**  */
	async instance() {
		this.native_ad = YouziCenter.getInstance().createBottomBanner(Laya.stage, {x: Laya.stage.width/2-320, y: Laya.stage.height-170}, false);
	}

	/**  */
	async destrory() {
		this.native_ad.destroy();
	};

	/**  */
	async show(pos?: Vector2) {
		if (!this.native_ad) {
			await this.instance();
		}
		if (this.native_ad) {
			if (pos) {
				this.native_ad.pos(pos.x, pos.y);
			}
			this.native_ad.visible = true;
		}
	};

	/**  */
	async hide() {
		if (this.native_ad) {
			this.native_ad.visible = false;
		}
	};
}


export class YouziFullScreenAdUnit extends ADUnit {
	/**  */
	async show(pos?: Vector2) {
		YouziCenter.getInstance().showFullScreenMatrix();
	};
}

export class FloatMainPushAdUnit extends ADUnit {

	private native_ad: YouziMainPush = null;

	async instance() {
		this.native_ad = YouziCenter.getInstance().createMainPush(Laya.stage, null);
	}

	/**  */
	async show(pos?: Vector2) {
		if (!this.native_ad) {
			await this.instance();
		}
		if (this.native_ad) {
			this.native_ad.removeSelf();
			if (pos) {
				this.native_ad.pos(pos.x, pos.y);
			}
			Laya.stage.addChild(this.native_ad);
		}
	};

	async hide() {
		if (this.native_ad) {
			this.native_ad.removeSelf();
		}
	}
}

export class YouziFloatMoreGameAdUnit extends ADUnit {

	private button: Laya.Button = null;
	private moreGameUI: Youzi_MoreGameUI = null;

	async instance() {
		this.button = YouziCenter.getInstance().createMoreGameButton(Laya.stage, null, true);
		this.moreGameUI = YouziCenter.getInstance().createMoreGameUI(Laya.stage, null);
		this.button.removeSelf();
	}

	async show(pos?: Vector2) {
		if (!this.button) {
			await this.instance();
		}
		if (this.button) {
			this.button.removeSelf();
			if (pos) {
				this.button.pos(pos.x, pos.y);
			}
			Laya.stage.addChild(this.button);
		}
	}

	async hide() {
		if (this.button) {
			this.button.removeSelf();
		}
	}
}

export class YouziSlideAdPanelADUnit extends ADUnit {
	private button: Laya.Button = null;
	private panel: YouziSlideWindow = null;

	async instance() {
		this.button = YouziCenter.getInstance().createSlideButton(Laya.stage, {x:60, y:Laya.stage.height / 2 - 40  , width:60,height:80}, true, true);
		this.panel = YouziCenter.getInstance().createSlideWindowUI(Laya.stage, null, true);
		this.button.removeSelf();
	}

	async show(pos?: Vector2) {
		if (!this.button) {
			await this.instance();
		}
		if (this.button) {
			this.button.removeSelf();
			if (pos) {
				this.button.pos(pos.x + this.button.width, pos.y);
			}
			Laya.stage.addChild(this.button);
		}
	}

	async hide() {
		if (this.button) {
			this.button.removeSelf();
		}
	}
}
