import { RunnerConfig } from "modules/dataModule/DinoModule";
import { MovieClip } from "modules/func/MovieClip";
import { RunnerMc } from "modules/func/RunnerMc";
import UI_hero from "view/raw/Main/UI_hero";
import MainScene from "../MainScene";
import { CollisionBox } from "./CollisionBox";

// Runner

export enum ERunType {
    CRASHED = 'CRASHED', // 
    DUCKING = 'DUCKING', // 
    JUMPING = 'JUMPING',
    RUNNING = 'RUNNING',
    WAITING = 'WAITING',
}

export const runnerCollisionBoxes = {
    DUCKING: [
        new CollisionBox(1, 18, 55, 25)
    ],
    RUNNING: [
        new CollisionBox(80, 80, 150, 150),
        // new CollisionBox(1, 18, 30, 9),
        // new CollisionBox(10, 35, 14, 8),
        // new CollisionBox(1, 24, 29, 5),
        // new CollisionBox(5, 30, 21, 4),
        // new CollisionBox(9, 34, 15, 4)
    ]
};
export default class Runner {
    public skinName: string;

    public _xPos = 0;
    public _yPos = 0;
    status = ERunType.WAITING; // 
    public groundYPos = 0; //  y 
    currentFrame = 0;  // 
    currentAnimFrames = [];       //  x 

    animStartTime = 0;            // 

    blinkDelay = 0;      //  
    blinkCount = 0;      // 

    jumping = false;              // 
    ducking = false;              // 
    jumpVelocity = 0;             // 
    reachedMinHeight = false;     // 
    speedDrop = false;            // 
    jumpCount = 0;                // 
    jumpspotX = 0;                //  x 
    minJumpHeight = 0;

    timer = 0;                    // 
    // msPerFrame = 1000 / FPS;      // 
    // config = Trex.config;
    // 
    public runnerSprite: RunnerMc = null;
    msPerFrame: any;

    constructor() {
        this.init();
    }

    animFrames = {
        WAITING: {
            frames: [44, 0],
            msPerFrame: 1000 / 3
        },
        RUNNING: {
            frames: [88, 132],
            msPerFrame: 1000 / 12
        },
        CRASHED: {
            frames: [220],
            msPerFrame: 1000 / 60
        },
        JUMPING: {
            frames: [0],
            msPerFrame: 1000 / 60
        },
        DUCKING: {
            frames: [262, 321],
            msPerFrame: 1000 / 8
        }
    }

    setData() {
    }

    updateDrawSprite() {
        this.runnerSprite = fgui.UIPackage.createObject("Main", "hero", RunnerMc) as RunnerMc;
        MainScene.inst.addChild(this.runnerSprite);
        let temp = runnerCollisionBoxes.RUNNING;
        temp.forEach((box: CollisionBox) => {
            let sp = new Laya.Sprite();
            this.runnerSprite.displayObject.addChild(sp);
            sp.graphics.drawRect(box.x, box.y, box.width, box.height, "#f00", "#0f0", 3);
        })

        // MovieClip.getMcByType(this.skinName, Laya.Handler.create(this, (mc: RunnerMc) => {
        //     this.runnerSprite = mc;
        // }), RunnerMc);
    }

    updateState(deltaTime: number, opt_status: ERunType = null) {
        this.timer += deltaTime;
        // Update the status.
        if (opt_status && opt_status != this.status) {
            this.status = opt_status;
            this.currentFrame = 0;
            // 
            this.runnerSprite.updateState(opt_status);
            if (opt_status == ERunType.WAITING) {
                this.animStartTime = this.getTimeStamp();
                this.setBlinkDelay();
            }
            // else {
            //     this.runnerSprite.play(opt_status)
            // }
        }
        if (MainScene.inst.playingIntro && this.xPos < RunnerConfig.START_X_POS) {
            this.xPos += Math.round((RunnerConfig.START_X_POS /
                RunnerConfig.INTRO_DURATION) * deltaTime);
        }
        if (this.status == ERunType.WAITING) {
            this.blink(this.getTimeStamp());
        }
        // Update the frame position.
        if (this.timer >= this.msPerFrame) {
            this.currentFrame = this.currentFrame ==
                this.currentAnimFrames.length - 1 ? 0 : this.currentFrame + 1;
            this.timer = 0;
        }
        // Speed drop becomes duck if the down key is still being pressed.
        if (this.speedDrop && this.yPos == this.groundYPos) {
            this.speedDrop = false;
            this.setDuck(true);
        }
    }

    init() {
        this.updateDrawSprite();
        this.setBlinkDelay();
        this.groundYPos = RunnerConfig.DEFAULT_HEIGH / 2 // - RunnerConfig.HEIGHT / 2;
        this.yPos = this.groundYPos;
        this.minJumpHeight = this.groundYPos - RunnerConfig.MIN_JUMP_HEIGHT;
        this.updateState(0, ERunType.WAITING);
    }

    set xPos(x: number) {
        this._xPos = x;
        this.runnerSprite.x = x;
    }

    get xPos() {
        return this._xPos;
    }

    get yPos() {
        return this._yPos;
    }

    set yPos(y: number) {
        this._yPos = y;
        this.runnerSprite.y = y;
    }

    // 
    blink(time) {
        var deltaTime = time - this.animStartTime;
        if (deltaTime >= this.blinkDelay) {
            this.runnerSprite.playBlink();
            this.setBlinkDelay();
            this.animStartTime = time;
        }
    }

    private setBlinkDelay() {
        this.blinkDelay = Math.ceil(Math.random() * RunnerConfig.BLINK_TIMING);
    }

    private getTimeStamp() {
        return Date.now();
        // return IS_IOS ? new Date().getTime() : performance.now();
    }

    /**
 * @param {boolean} isDucking.
 */
    setDuck(isDucking) {
        if (isDucking && this.status != ERunType.DUCKING) {
            this.updateState(0, ERunType.DUCKING);
            this.ducking = true;
        } else if (this.status == ERunType.DUCKING) {
            this.updateState(0, ERunType.RUNNING);
            this.ducking = false;
        }
    }

    /**
    * @param {number} deltaTime
    * @param {number} speed
    */
    updateJump(deltaTime) {
        let msPerFrame = this.animFrames[this.status].msPerFrame;
        let framesElapsed = deltaTime / msPerFrame;
        // Speed drop makes Trex fall faster.
        if (this.speedDrop) {
            this.yPos += Math.round(this.jumpVelocity *
                RunnerConfig.SPEED_DROP_COEFFICIENT * framesElapsed);
        } else {
            this.yPos += Math.round(this.jumpVelocity * framesElapsed);
        }
        this.jumpVelocity += RunnerConfig.GRAVITY * framesElapsed;
        // Minimum height has been reached.
        if (this.yPos < this.minJumpHeight || this.speedDrop) {
            this.reachedMinHeight = true;
        }
        // Reached max height
        if (this.yPos < RunnerConfig.MAX_JUMP_HEIGHT || this.speedDrop) {
            this.endJump();
        }
        // Back down at ground level. Jump completed.
        if (this.yPos > this.groundYPos) {
            this.reset();
            this.jumpCount++;
        }
        this.updateState(deltaTime);
    }

    startJump() {
        if (!this.jumping) {
            this.updateState(0, ERunType.JUMPING);
            // Tweak the jump velocity based on the speed.
            this.jumpVelocity = RunnerConfig.INITIAL_JUMP_VELOCITY - (MainScene.inst.speed / 10);
            this.jumping = true;
            this.reachedMinHeight = false;
            this.speedDrop = false;
        }
    }

    // 
    setSpeedDrop() {
        this.speedDrop = true;
        this.jumpVelocity = 1;
    }

    endJump() {
        if (this.reachedMinHeight &&
            this.jumpVelocity < RunnerConfig.DROP_VELOCITY) {
            this.jumpVelocity = RunnerConfig.DROP_VELOCITY;
        }
    }

    reset() {
        this.yPos = this.groundYPos;
        this.jumpVelocity = 0;
        this.jumping = false;
        this.ducking = false;
        this.updateState(0, ERunType.RUNNING);
        this.speedDrop = false;
        this.jumpCount = 0;
    }
}