// 
export function array_erase<T>(arr: T[], ele: T): T[] {
	return arr.filter(item => item != ele);
}

/**  */
export function shuffle_array(array: any[]) {
	for (let i = array.length - 1; i > 0; i--) {
		const rand = Math.floor(Math.random() * (i + 1));
		[array[i], array[rand]] = [array[rand], array[i]];
	}
}

/**
 * 
 * @param sp 
 * @param name 
 */
export function findChild(sp, name) {
	if (sp.name == name)
		return sp;
	else
		return _findChild(sp._children, name);
}
function _findChild(spArr, name) {
	let arr = [];
	for (var i = 0; i < spArr.length; i++) {
		var child = spArr[i];
		if (child.name == name) {
			return child;
		}
		else if (child.numChildren) {
			arr = arr.concat(child._children);
		}
	}
	if (!arr.length)
		return null;
	return _findChild(arr, name);
}

/**
 * 
 *
 * @export
 * @param {number} value 
 * @param {number} min 
 * @param {number} max 
 * @returns {number} 
 */
export function clamp(value: number, min: number, max: number): number {
	if (value <= max && value >= min)
		return value;
	if (value > max)
		return max;
	if (value < min)
		return min;
}

/**  */
export function random_range(min: number, max: number): number {
	return Math.random() * (max - min) + min;
}


export function getRandomIntNum(min, max) {
	return Math.floor(Math.random() * (max - min + 1)) + min;
}

/**
* 
*
* @export
* @param {number} second
* @returns {string}
*/
export function format_time_duration(second: number): string {
	let m: any = Math.floor(second / 60);
	let s: any = Math.floor(second - m * 60);
	if (m < 10)
		m = `0${m}`;
	if (s < 10)
		s = `0${s}`;
	return `${m}:${s}`;
}
