import APIProvider, { RequetMethod, FloadAdItem, provider } from "../APIProvider";
import Game from "xengine/game/Game";
import Platform from "modules/platform/platform";
import WechatPlatform from "modules/platform/wechat/WechatPlatform";
import GameConfig, { PlatformValue } from "config";
import { PayType, PayPoint } from "../payment";
import CryptoJS from "./hmac-sha256";
import md5 from "./md5";
import Module from "xengine/game/Module";
interface ADExposure {
	ad_id: number,
	location_id: number,
	num: number
}

@provider("yunqu")
export default class YunquProvider extends APIProvider {

	readonly base_url: string = "https://api.yz061.com/";
	channel: string = "own";
	from_appid: string = "";
	openid: string = '';

	floadAds: any[] = [];
	yunquConfigs: any[] = [];
	shareSeed: any = null;
	floatAdsSeed: any[] = [];

	constructor(id: string) {
		super('qgdzz');
	}

	async initialize() {
		if (PlatformValue.WECHAT == GameConfig.platform) {
			this.channel = 'own';
			let options = Game.inst.get_module<WechatPlatform>(Platform).get_launch_option();
			if (options.query && options.query.channel) {
				this.channel = options.query.channel;
			}
			if (options.referrerInfo && options.referrerInfo.appId) {
				this.from_appid = options.referrerInfo.appId;
			}
		}
		console.log("API", "");
		this.yunquConfigs = await this.get_app_config();
		if (this.yunquConfigs && this.yunquConfigs.length) {
			for (const cfg of this.yunquConfigs) {
				if (cfg.matter_type == 1) {
					this.floatAdsSeed.push(cfg);
				} else if (cfg.matter_type == 2) {
					this.shareSeed = cfg;
				}
			}
		}
		console.log("API", "", this.yunquConfigs);
		let user = await Game.inst.get_module<Platform>(Platform).login();
		console.log("API", "", user);
		await this.login(user.uuid);
		user = await Game.inst.get_module<Platform>(Platform).get_user_info();
		console.log("API", "", user);
		const uid = this.openid ? this.openid : 'undefined';
		if (this.floatAdsSeed.length) {
			for (const item of this.floatAdsSeed) {
				let ads = await super.request('get', item.url + `&uid=${uid}`);
				for (const itm of ads) {
					this.float_ads.push({
						id: itm.ad_appid,
						title: itm.ad_name,
						icon: itm.ad_img,
						url: itm.ad_path,
						data: itm,
						location_id:item.location_id,
						location_flg:item.location_flg,
					});
				}
			}
			console.log("API", "", this.float_ads);
			if (typeof wx != 'undefined') {
				wx['onHide'](this.report_ad_exposure.bind(this));
			}

		}
		if (this.shareSeed) {
			const share_arr = await super.request('get', this.shareSeed.url + `&uid=${uid}`);
			if (share_arr && share_arr.length) {
				const cfg = share_arr[0];
				this.share_info = {
					title: cfg.share_title,
					message: cfg.share_title,
					url: cfg.share_path,
					image_url: cfg.share_img,
					data: cfg
				};
				const p = Game.inst.get_module<Platform>(Platform);
				if (p instanceof WechatPlatform) {
					p.set_share_config(this.share_info);
				}
			}
			console.log("API", "", this.share_info);
		}

		(Game.inst.modules['commerce'] as Module).on("PAY_DONE", null, (point: PayPoint, type: PayType)=> {
			console.log("API", "");
			this.report_pay_action(type);
		});
	}

	async login(token: string) {
		const user = await Game.inst.get_module<Platform>(Platform).get_user_info();
		const data = await this.request('get', 'auth', {code: token});
		this.openid = data.openid || "undefined";
		if (this.openid) {
			user.uuid = this.openid;
		}
		await this.request('get', 'daily', {uid: data.openid});
		this.user_info = user;
		return user;
	}

	async request(method: RequetMethod, api: string, body?: object, add_sign = false) {
		let new_body = {flg: this.gameid, channel: this.channel, appid: this.from_appid};
		if (typeof(body) === 'object') {
			new_body = {...new_body, ...body};
			new_body['uid'] = this.openid;
		}
		if (add_sign) {
			let sign = this.generate_sign(new_body);
			api += '?sign=' + sign;
		}
		let ret = await super.request(method, api, new_body);
		console.log("API", "", api, new_body, ret);
		return ret;
	}

	private get_app_config() {
		return this.request('get', `game/${this.gameid}`);
	}

	/**  */
	protected parse_request_response(method: RequetMethod, api: string, url: string, response: any) {
		if (response.result) {
			return response.result;
		}
		return response;
	}

	async click_float_ad_item(item: FloadAdItem) {
		let status = '';
		try {
			const platform = Game.inst.get_module<Platform>(Platform);
			await platform.navigate_to_app(item.id, item.url);
			status = 'cb';
		} catch (error) {
			console.warn("APP", error);
		}
		this.request("post", "reportad", {status, gflg: this.gameid, ad_id: item.data.ad_id, location_id:item.location_id, channel: this.channel});
		console.log("API", "");
		if (!status) {
			throw "";
		}
	}

	private exposure = new Map<number, Map<number, ADExposure>>();
	private async report_ad_exposure() {
		if (!this.exposure.size) return;
		let data: ADExposure[] = [];
		for (const d of this.exposure.values()) {
			for (const dd of d.values()) {
				data.push(dd);
			}
		}
		await this.request("post", "exposure", {
			data,
			gflg: this.gameid,
			channel: this.channel,
		});
		this.exposure.clear();
		console.log("API", "");
	}

	public count_exposure(ads: {ad_id: number, location_id: number}[]) {
		for (const ad of ads) {
			let location = this.exposure.get(ad.location_id) || new Map<number, ADExposure>();
			let cfg = location.get(ad.ad_id) || {ad_id: ad.ad_id, location_id: ad.location_id, num: 0};
			cfg.num += 1;
			location.set(ad.ad_id, cfg);
			this.exposure.set(ad.location_id, location);
		}
	}

	/**  */
	public async report_pay_action(type: PayType) {
		let loc = '';
		if (type == PayType.SHARE) {
			loc = 'share';
		} else if (type == PayType.REWARD_VIDEO) {
			loc = 'video';
		}
		if (loc) {
			await this.request('post', 'stat', {loc}, true);
		}
	}

	public generate_sign(body: object) {
		let keyList = Object.keys(body).sort();
        let paramList = [];
        for (let key of keyList) {
            paramList.push(key + "=" + body[key]);
        }
        let signStr = paramList.join("&");
        return CryptoJS.HmacSHA256(signStr, md5.hex_md5(this.openid + this.gameid)).toString();
	}

}
