import { mc } from "./mc";
import { ERunType } from "view/main/com/Runner";
import { once } from "cluster";

export class RunnerMc extends mc {
    getNowMovies(): fgui.GMovieClip[] {
        throw new Error("Method not implemented.");
    } //
    private _state: fairygui.Controller;
    private _isGlass: boolean = false;//

    private _dire: string = ERunType.WAITING;
    protected constructFromXML(xml: any): void {
        super.constructFromXML(xml);
        this._state = this.getController('c1');
    }

    public getNowMovie(): fgui.GMovieClip {
        let mv = this.getChild(this._dire);
        if (mv instanceof fgui.GMovieClip) {
            return mv;
        }
        return null;
    }

    public stop() {
        let mv = this.getNowMovie();
        mv && (mv.playing = false);
    }

    public playBlink() {
        this.play(ERunType.WAITING, true);
    }

    public play(lifeState: string = ERunType.WAITING, once?: boolean, handler?: Laya.Handler, dispose: boolean = true, dire?: number) {
        this.stop();
        this.updateState(lifeState);
        super.play(lifeState, once, handler, dispose);
    }

    public updateState(lifeState: string) {
        this._state.selectedPage = lifeState;
    }

    public get dire() {
        return this._dire;
    }

    get mc() {
        return this._movies;
    }
}