/**  */
export type Nullable < T > = T | null;

/**
 * 
 */
let $hashCount: number = 1;

/**
 * 
 * `hashCode`
 */
export class HashObject {

	/**
	 *  HashObject 
	 */
	public constructor() {
		this.$hashCode = $hashCount++;
	}

	private $hashCode: number;

	/**
	 * ,`hashCode` `1`
	 */
	public get hashCode(): number {
		return this.$hashCode;
	}
}
