// 
export class CollisionBox {
    x: number;
    y: number;
    width: number;
    height: number;

    constructor(x, y, w, h) {
        this.x = parseInt(x);
        this.y = parseInt(y);
        this.width = parseInt(w);
        this.height = parseInt(h);
    }

    //  0,0,0,0;1,1,1,1
    static cloneCollisionBoxes(config: string, scale: number = 1): CollisionBox[] {
        let temp = config.split(";");
        let boxes: CollisionBox[] = [];
        temp.forEach((str: string, index: number) => {
            let arr = str.split(",");
            boxes[index] = new CollisionBox(parseInt(arr[0]), parseInt(arr[1]), parseInt(arr[2]) * scale, parseInt(arr[3]) * scale)
        })
        return boxes;
    }
}