import MainScene from "../MainScene";
import { Obstacle } from "./Obstacle";
import { getRandomIntNum } from "xengine/utils";
import { RunnerConfig } from "modules/dataModule/DinoModule";
import UI_obstacleCom from "view/raw/Main/UI_obstacleCom";

// 
export class Horizons {
    public _hroizon1: fgui.GLoader;
    public _hroizon2: fgui.GLoader;

    public obstacles: Obstacle[] = [];
    private xPos = [0, 0];
    obstacleHistory = []
    private _width = 2048;
    private _hight = 12;
    private yPos = 0;

    constructor() {
        this._hroizon1 = new fgui.GLoader();
        this._hroizon2 = new fgui.GLoader();
        this._hroizon1.url = "assets/ui/plot/hroizon.jpg";
        this._hroizon2.url = "assets/ui/plot/hroizon.jpg";
        MainScene.inst.addChildAt(this._hroizon1, 0);
        MainScene.inst.addChildAt(this._hroizon2, 0);
    }

    /**
     * @param {number} deltaTime
     * @param {boolean} updateObstacles Used as an override to prevent
     */
    update(deltaTime, updateObstacles) {
        // this.runningTime += deltaTime;
        this.updateHroizon(deltaTime);
        // this.updateClouds(deltaTime);
        if (updateObstacles) {
            this.updateObstacles(deltaTime);
        }
    }

    updateHroizon(deltaTime) {
        var increment = MainScene.inst.speed * (MainScene.inst.FPS / 1000) * deltaTime;
        if (this.xPos[0] <= 0) {
            this.updateHroizonXpos(0, increment);
        } else {
            this.updateHroizonXpos(1, increment);
        }
    }

    updateHroizonXpos(pos, increment) {
        var line1 = pos;
        var line2 = pos == 0 ? 1 : 0;
        this.xPos[line1] -= increment;
        this.xPos[line2] = this.xPos[line1] + this._width;
        if (this.xPos[line1] <= -this._width) {
            this.xPos[line1] += this._width * 2;
            this.xPos[line2] = this.xPos[line1] - this._width;
            // this.sourceXPos[line1] = this.getRandomType() + this.spritePos.x;
        }
        this._hroizon1.x = this.xPos[line1];
        this._hroizon2.x = this.xPos[line2];
        this._hroizon1.y = this._hroizon2.y = this.yPos;
    }

    /**
  * Update the obstacle positions.
  * @param {number} deltaTime
  */
    updateObstacles(deltaTime) {
        // Obstacles, move to Horizon layer.
        var updatedObstacles = this.obstacles.slice(0);
        for (var i = 0; i < this.obstacles.length; i++) {
            var obstacle = this.obstacles[i];
            obstacle.update(deltaTime);
            // Clean up existing obstacles.
            if (obstacle.remove) {
                updatedObstacles.shift();
            }
        }
        this.obstacles = updatedObstacles;
        if (this.obstacles.length > 0) {
            var lastObstacle = this.obstacles[this.obstacles.length - 1];
            if (lastObstacle && !lastObstacle.followingObstacleCreated &&
                lastObstacle.isVisible() &&
                (lastObstacle.x + lastObstacle.width + lastObstacle.gap) < this._width) {
                this.addNewObstacle();
                lastObstacle.followingObstacleCreated = true;
            }
        } else {
            this.addNewObstacle();
        }
    }

    addNewObstacle() {
        let allType = MainScene.inst.dinoModule.staticData.Obstacle_data;
        var obstacleTypeIndex = getRandomIntNum(0, allType.length - 1);
        var obstacleType = allType[obstacleTypeIndex];
        // Check for multiples of the same type of obstacle.
        // Also check obstacle is available at current speed.
        if (this.duplicateObstacleCheck(obstacleType.type) ||
            MainScene.inst.speed < obstacleType.minSpeed) {
            this.addNewObstacle();
        } else {
            let obstacle = fgui.UIPackage.createObject("Main", "obstacleCom", Obstacle) as Obstacle;
            MainScene.inst.addChild(obstacle);
            this.obstacles.push(obstacle);
            obstacle.setData(obstacleType)
            // new Obstacle(obstacleType,
            //     obstacleSpritePos, this.dimensions,
            //     this.gapCoefficient, currentSpeed));
            this.obstacleHistory.unshift(obstacleType.type);
            if (this.obstacleHistory.length > 1) {
                this.obstacleHistory.splice(RunnerConfig.MAX_OBSTACLE_DUPLICATION);
            }
        }
    }

    // 
    duplicateObstacleCheck(nextObstacleType) {
        var duplicateCount = 0;
        for (var i = 0; i < this.obstacleHistory.length; i++) {
            duplicateCount = this.obstacleHistory[i] == nextObstacleType ?
                duplicateCount + 1 : 0;
        }
        return duplicateCount >= RunnerConfig.MAX_OBSTACLE_DUPLICATION;
    }

    reset() {
        this.obstacles = [];
        this.xPos[0] = 0;
        this.xPos[1] = this._width;
    }
}