import { HashObject } from "./HashObject";

/**
 * 
 *
 *  `Handler.create()`  Handler  Handler.recover() 
 */
export class Handler extends HashObject {

	/** handler */
	protected static _pool: any[] = [];
	/** handlerID */
	private static _gid: number = 1;

	/** (this)*/
	caller: any;
	/** */
	method: Function;
	/** */
	args: any[];
	/** truerecover()false */
	once: boolean = false;

	/**@private */
	protected _id: number = 0;

	/**
	 *  <code>Handler</code> 
	 * @param	caller 
	 * @param	method 
	 * @param	args 
	 * @param	once 
	 */
	constructor(caller: any = null, method: Function = null, args: any[] = null, once: boolean = false) {
		super();
		this.setTo(caller, method, args, once);
	}

	/**
	 * 
	 * @param	caller (this)
	 * @param	method 
	 * @param	args 
	 * @param	once truerecover()
	 * @return   handler 
	 */
	setTo(caller: any, method: Function, args: any[], once: boolean): Handler {
		this._id = Handler._gid++;
		this.caller = caller;
		this.method = method;
		this.args = args;
		this.once = once;
		return this;
	}

	/**
	 * 
	 */
	run(): any {
		if (this.method == null) return null;
		var id: number = this._id;
		var result: any = this.method.apply(this.caller, this.args);
		this._id === id && this.once && this.recover();
		return result;
	}

	/**
	 * 
	 * @param	data Array()
	 */
	runWith(data: any): any {
		if (this.method == null) return null;
		var id: number = this._id;
		if (data == null)
			var result: any = this.method.apply(this.caller, this.args);
		else if (!this.args && !data.unshift) result = this.method.call(this.caller, data);
		else if (this.args) result = this.method.apply(this.caller, this.args.concat(data));
		else result = this.method.apply(this.caller, data);
		this._id === id && this.once && this.recover();
		return result;
	}

	/**
	 * 
	 */
	clear(): Handler {
		this.caller = null;
		this.method = null;
		this.args = null;
		return this;
	}

	/**
	 *  Handler 
	 */
	recover(): void {
		if (this._id > 0) {
			this._id = 0;
			Handler._pool.push(this.clear());
		}
	}

	/**
	 * Handleroncefalse
	 * @param	caller (this)
	 * @param	method 
	 * @param	args 
	 * @param	once truerecover()true
	 * @return  handler
	 */
	static create(caller: any, method: Function, args: any[] = null, once: boolean = true): Handler {
		if (Handler._pool.length) return Handler._pool.pop().setTo(caller, method, args, once);
		return new Handler(caller, method, args, once);
	}
}
