import UI_Splash from "view/raw/Splash/UI_Splash";
import Event, { once } from "xengine/events/Event";
import { Tween, Easing } from "@tweenjs/tween.js";
import XEngine from "xengine/XEngine";
import Game, { Events, ELoadPro } from "xengine/game/Game";

export default class SplashScene extends UI_Splash {
	constructFromResource() {
		super.constructFromResource();
		this.setSize(fairygui.GRoot.inst.width, fairygui.GRoot.inst.height, true);
		once(this.displayObject, Event.ADDED, this, this.onReady);
	}

	private onReady() {
		this.m_progress.value = 0;
		this.m_progress.tweenValue(45, 1);
		// 
		if (Game.inst.stated) {
			this.loadMain();
		}
		else {
			Game.inst.on(Events.LOGIC_STARTED, this, () => {
				this.loadMain();
			})
		}
		Game.inst.on(ELoadPro.RESLOADSUC, this, () => {
			this.m_progress.value = 100;
			console.log("")
			XEngine.inst.scene_manager.replace("Main");
		})
	}

	private loadMain() {
		this.m_progress.tweenValue(95, 2);
		XEngine.inst.scene_manager.push('Main', true);
	}
}
