import UI_Main from "view/raw/Main/UI_Main";
import Game, { ELoadPro } from "xengine/game/Game";
import DinoModule, { RunnerConfig } from "modules/dataModule/DinoModule";
import Runner, { runnerCollisionBoxes, ERunType } from "./com/Runner";
import { Horizons } from "./com/Horizon";
import { CollisionBox } from "./com/CollisionBox";
import { Obstacle } from "./com/Obstacle";
import GameOverView from "./panel/gameOverView";
import UI_gameOverView from "view/raw/Main/UI_gameOverView";

export default class MainScene extends UI_Main {
	static inst: MainScene = null;

	speed: number = RunnerConfig.SPEED;

	public dinoModule: DinoModule;
	private _hero: Runner;
	private _horizon: Horizons;
	distanceRan: number = 0;
	playCount: number; // 
	public playingIntro: boolean = false; // 
	private allPanels = [];
	activated = false; // 
	started = false;
	crashed = false; // 
	public FPS = 60;
	msPerFrame = 1000 / this.FPS;

	gameOverView: GameOverView;

	constructFromResource() {
		super.constructFromResource();
		MainScene.inst = this;
		this.dinoModule = Game.inst.get_module<DinoModule>(DinoModule);
		this.setSize(fairygui.GRoot.inst.width, fairygui.GRoot.inst.height, true);
		// FairyGUIBinder.onClick(this.m_bt_dialog, XEngine.inst.fairygui_helper, XEngine.inst.fairygui_helper.show_dialog, TestDialog, 'Dialog from MainScene');
		// FairyGUIBinder.onClick(this.m_bt_toast, XEngine.inst.fairygui_helper.toast, XEngine.inst.fairygui_helper.toast.popup, 'Toast from MainScene');
		this.m_startBtn.onClick(this, this.startRunGame);
		this._hero = new Runner();
		this._horizon = new Horizons();
		Laya.stage.on(Laya.Event.KEY_DOWN, this, this.onKeyDown);
		Laya.stage.on(Laya.Event.KEY_UP, this, this.onKeyUp);
		Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.onKeyDown);
		Laya.stage.on(Laya.Event.MOUSE_UP, this, this.onKeyUp);
		// this.on(Laya.Event.DRAG_START, this, this.onKeyDown);
		// this.on(Laya.Event.DRAG_END, this, this.onKeyUp);
	}

	async startRunGame() {
		if (this.started) return;
		this.play();
		Laya.timer.frameLoop(1, this, this.runGameUpdate);
	}

	time: number = 0;
	runningTime: number = 0; // 
	private runGameUpdate() {
		let now = Laya.Browser.now();
		let deltaTime = now - (this.time || now);
		this.time = now;
		if (this.activated) {
			if (this._hero.jumping) {
				this._hero.updateJump(deltaTime);
			}
			this.runningTime += deltaTime;
			var hasObstacles = this.runningTime > RunnerConfig.CLEAR_TIME;
			// First jump triggers the intro.
			if (this._hero.jumpCount == 0 && !this.playingIntro) {
				this.playIntro();
			}
			// The horizon doesn't move until the intro is over.
			if (this.playingIntro) {
				this._horizon.update(0, hasObstacles);
			} else {
				deltaTime = !this.started ? 0 : deltaTime;
				this._horizon.update(deltaTime, hasObstacles);
			}
			// Check for collisions.
			var collision = hasObstacles &&
				this.checkForCollision(this._horizon.obstacles[0], this._hero);
			if (!collision) {
				this.distanceRan += this.speed * deltaTime / this.msPerFrame;
				this.m_gradeTxt.text = Math.floor(this.distanceRan).toString();
				if (this.speed < RunnerConfig.MAX_SPEED) {
					this.speed += RunnerConfig.ACCELERATION;
				}
			} else {
				this.gameOver();
			}
			// 
			// var playAcheivementSound = this.distanceMeter.update(deltaTime,
			// 	Math.ceil(this.distanceRan));
			// if (playAcheivementSound) {
			// 
			// this.playSound(this.soundFx.SCORE);
			// }
		}
		if (!this.crashed) {
			this._hero.updateState(deltaTime);
		}
	}

	gameOver() {
		// this.playSound(this.soundFx.HIT);
		this.stop();
		this.crashed = true;
		// this.distanceMeter.acheivement = false;
		this._hero.updateState(100, ERunType.CRASHED);
		// show gameOverpanel
		this.showGameOverView(this._horizon.obstacles[0]);
		this.m_gradeTxt.text = Math.floor(this.distanceRan).toString();
		// Update the high score.
		if (this.distanceRan > this.dinoModule.highestScore) {
			this.dinoModule.highestScore = Math.ceil(this.distanceRan);
			// this.distanceMeter.setHighScore(this.highestScore);
		}
		// Reset the time clock.
		this.time = Laya.Browser.now();
	}

	boxCompare(tRexBox, obstacleBox) {
		let crashed = false;
		let tRexBoxX = tRexBox.x;
		let tRexBoxY = tRexBox.y;
		let obstacleBoxX = obstacleBox.x;
		let obstacleBoxY = obstacleBox.y;
		// Axis-Aligned Bounding Box method.
		if (tRexBox.x < obstacleBoxX + obstacleBox.width &&
			tRexBox.x + tRexBox.width > obstacleBoxX &&
			tRexBox.y < obstacleBox.y + obstacleBox.height &&
			tRexBox.height + tRexBox.y > obstacleBox.y) {
			crashed = true;
		}
		return crashed;
	}

	checkForCollision(obstacle: Obstacle, tRex: Runner, opt_canvasCtx = true) {
		var obstacleBoxXPos = Laya.stage.width + obstacle.x;
		// Adjustments are made to the bounding box as there is a 1 pixel white
		// border around the t-rex and obstacles.
		var tRexBox = new CollisionBox(
			tRex.xPos + 1,
			tRex.yPos + 1,
			RunnerConfig.WIDTH - 2,
			RunnerConfig.HEIGHT - 2);
		var obstacleBox = new CollisionBox(
			obstacle.x + 1,
			obstacle.y + 1,
			obstacle.data.width * obstacle.size - 2,
			obstacle.data.height - 2);
		if (this.boxCompare(tRexBox, obstacleBox)) {
			var collisionBoxes = obstacle.collisionBoxes;
			var tRexCollisionBoxes = tRex.ducking ?
				runnerCollisionBoxes.DUCKING : runnerCollisionBoxes.RUNNING;
			// Detailed axis aligned box check.
			for (var t = 0; t < tRexCollisionBoxes.length; t++) {
				for (var i = 0; i < collisionBoxes.length; i++) {
					// Adjust the box to actual positions.
					var adjTrexBox =
						this.createAdjustedCollisionBox(tRexCollisionBoxes[t], tRexBox);
					var adjObstacleBox =
						this.createAdjustedCollisionBox(collisionBoxes[i], obstacleBox);
					var crashed = this.boxCompare(adjTrexBox, adjObstacleBox);
					if (crashed) {
						return [adjTrexBox, adjObstacleBox];
					}
				}
			}
		}
		return false;
	}

	createAdjustedCollisionBox(box, adjustment) {
		return new CollisionBox(
			box.x + adjustment.x,
			box.y + adjustment.y,
			box.width,
			box.height);
	}

	playIntro() {
		if (!this.started && !this.crashed) {
			this.playingIntro = true;
			// 
			this.activated = true;
			this.started = true;
			// Laya.timer.once(1000, this, this.startGame);
			this.startGame();
		} else if (this.crashed) {
			this.restart();
		}
	}

	startGame() {
		this.runningTime = 0;
		this.playingIntro = false;
		this.playCount++;
		// Handle tabbing off the page. Pause the current game.
		Laya.stage.on(Laya.Event.FOCUS, this, this.onVisibilityChange, [true]);
		Laya.stage.on(Laya.Event.BLUR, this, this.onVisibilityChange, [false]);
		// this.on(Laya.Event.VISIBILITY_CHANGE, this, this.onVisibilityChange, [true]);
	}

	onVisibilityChange(bool) {
		if (!bool) {
			this.stop();
		} else if (!this.crashed) {
			this._hero.reset();
			this.play();
		}
	}

	stop() {
		this.activated = false;
	}

	play() {
		if (!this.crashed) {
			this.activated = true;
			this._hero.updateState(0, ERunType.RUNNING);
			this.time = Laya.Browser.now();
			this.runGameUpdate();
		}
	}
	keycodes = {
		JUMP: { '38': 1, '32': 1 },  // Up, spacebar
		DUCK: { '40': 1 },  // Down
		RESTART: { '13': 1 }  // Enter
	}

	// 
	onKeyUp(e: Laya.Event) {
		if (!this.started) return;
		var keyCode = String(e.keyCode);
		var isjumpKey = this.keycodes.JUMP[keyCode] ||
			e.type == Laya.Event.MOUSE_DOWN;
		if (this.activated && isjumpKey) {
			this._hero.endJump();
		} else if (this.keycodes.DUCK[keyCode]) {
			this._hero.speedDrop = false;
			this._hero.setDuck(false);
		} else if (this.crashed) {
			// Check that enough time has elapsed before allowing jump key to restart.
			var deltaTime = Laya.Browser.now() - this.time;
			if (this.keycodes.RESTART[keyCode] ||
				(deltaTime >= RunnerConfig.GAMEOVER_CLEAR_TIME &&
					this.keycodes.JUMP[keyCode])) {
				this.restart();
			}
		} else if (!this.activated && isjumpKey) {
			// Reset the jump state
			this._hero.reset();
			this.play();
		}
	}

	onKeyDown(e: Laya.Event) {
		if (!this.started) return;
		if (!this.crashed && (this.keycodes.JUMP[e.keyCode] ||
			e.type == Laya.Event.MOUSE_DOWN)) {
			if (!this.activated) {
				// this.loadSounds();
				this.activated = true;
				//window.errorPageController.trackEasterEgg();
			}
			if (!this._hero.jumping && !this._hero.ducking) {
				// this.playSound(this.soundFx.BUTTON_PRESS);
				this._hero.startJump();
			}
		}
		// if (this.crashed && e.type == Runner.events.TOUCHSTART &&
		// 	e.currentTarget == this.containerEl) {
		// 	this.restart();
		// }
		if (this.activated && !this.crashed && this.keycodes.DUCK[e.keyCode]) {
			// e.preventDefault();
			if (this._hero.jumping) {
				// Speed drop, activated only when jump key is not pressed.
				this._hero.setSpeedDrop();
			} else if (!this._hero.jumping && !this._hero.ducking) {
				// Duck.
				this._hero.setDuck(true);
			}
		}
	}

	restart() {
		this.playCount++;
		this.runningTime = 0;
		this.activated = true;
		this.crashed = false;
		this.distanceRan = 0;
		this.setSpeed(RunnerConfig.SPEED);
		this.time = Laya.Browser.now();
		// this.distanceMeter.reset(this.highestScore);
		this._horizon.reset();
		this._hero.reset();
		// this.playSound(this.soundFx.BUTTON_PRESS);
		this.runGameUpdate();
	}

	setSpeed(opt_speed) {
		var speed = opt_speed || this.speed;
		// Reduce the speed on smaller mobile screens.
		if (Laya.stage.width < RunnerConfig.DEFAULT_WIDTH) {
			var mobileSpeed = speed * Laya.stage.width / RunnerConfig.DEFAULT_WIDTH *
				RunnerConfig.MOBILE_SPEED_COEFFICIENT;
			this.speed = mobileSpeed > speed ? speed : mobileSpeed;
		} else if (opt_speed) {
			this.speed = opt_speed;
		}
	}

	showGameOverView(obstacle: Obstacle) {
		this.gameOverView = this.gameOverView || this.createObj(UI_gameOverView.URL, GameOverView);
		this.gameOverView.setData(obstacle);
		this.addChild(this.gameOverView);
		this.removeOtherPanel(this.gameOverView);
		return this.gameOverView;
	}

	// showGameFailView() {
	// 	this.gameFailView = this.gameFailView || this.createObj(UI_GameOverView.URL, GameOverView);
	// 	this.addChild(this.gameFailView);
	// 	this.removeOtherPanel(this.gameFailView);
	// 	return this.gameFailView;
	// }

	// showMainView() {
	// 	this.mianView = this.mianView || this.createObj(UI_MainView.URL, MianView);
	// 	this.addChild(this.mianView);
	// 	this.removeOtherPanel(this.mianView);
	// 	return this.mianView;
	// }

	parentRemove(com: any) {
		if (com && com.parent) {
			com.parent.removeChild(com);
		}
	}

	removeOtherPanel(com: any) {
		this.allPanels.forEach((view: any) => {
			if (view != com) {
				this.parentRemove(view);
			}
		})
	}

	private createObj(url, cls: any): any {
		let panel = fairygui.UIPackage.createObjectFromURL(url, cls) as any;
		this.allPanels.push(panel);
		return panel;
	}
}