import { ERunType } from "view/main/com/Runner";

export abstract class mc extends fgui.GComponent { //    
    protected _frame: number = -1;
    protected _movies: fgui.GMovieClip[];

    protected constructFromXML(xml: any): void {
        super.constructFromXML(xml);
        this._movies = this._children as fgui.GMovieClip[];
        // this._movies.map((mv: fgui.GMovieClip) => {
        //     mv.setPivot(0.5, 0.5, true);
        //     mv.setXY(0, 0);
        // })
        // this.setXY(this.width / 2, this.height / 2);
        this.opaque = false;
    }

    set playing(bool: boolean) {
        let mv = this.getNowMovie();
        mv.playing = bool;
    }

    get playing() {
        let mv = this.getNowMovie();
        return mv.playing;
    }

    abstract stop();
    abstract getNowMovie(): fgui.GMovieClip;

    play(lifeState: string = ERunType.WAITING, once?: boolean, handler?: Laya.Handler, dispose: boolean = true) {
        let mv = this.getNowMovie();
        if (mv) {
            mv.playing = true;
            if (once) {
                mv.setPlaySettings(0, -1, 1, -1, Laya.Handler.create(this, (endHandler: Laya.Handler) => {
                    endHandler && endHandler.run();
                    dispose && this.dispose();
                }, [handler]));
            }
            else {
                mv.setPlaySettings(0, -1, 0, -1);
            }
        }
    }

    get frame() {
        let mv = this.getNowMovie();
        if (mv) {
            return mv.frame;
        }
        return -1;
    }

    set frame(frame: number) {
        if (frame && frame >= 0) {
            this._frame = frame;
            let mv = this.getNowMovie();
            mv.frame = frame;
        }
    }

    public dispose() {
        this._movies = null;
        this._frame = -1;
        super.dispose();
    }

    get packName() {
        return this.packageItem.owner.name;
    }

    get packItemName() {
        return this.packageItem.name;
    }

    set children(value: any[]) {
        this._children = value;
    }

    get children() {
        return this._children;
    }
}