import { game_module } from "xengine/game/Module";
import { StaticData } from "modules/common/StaticData";

@game_module('data')
export default class GameStaticData extends StaticData {
    Obstacle_data: ObstacleData[] = [];
    config: any = null;

    Obstacles_data: { [key: string]: ObstacleData } = {};

    protected get_res_member_map(): Object {
        return {
            "assets/data/json/Level.json": "level_data",
            "assets/data/json/Obstacle.json": "Obstacle_data",
        };
    }

    protected on_config_loaded() {
        super.on_config_loaded();
        for (const lv of this.Obstacle_data) {
            this.Obstacles_data[lv.type] = lv;
        }
    }

    /**  */
    get_obstacle_data(type: string) {
        return this.Obstacles_data[type];
    }
}