import MainScene from "../MainScene";
import UI_obstacleCom from "view/raw/Main/UI_obstacleCom";
import { getRandomIntNum } from "xengine/utils";
import { CollisionBox } from "./CollisionBox";
import { RunnerConfig } from "modules/dataModule/DinoModule";

// 
enum EObstacleType {
    CACTUS_SMALL = 'CACTUS_SMALL',
    CACTUS_LARGE = 'CACTUS_LARGE',
    PTERODACTYL = 'PTERODACTYL',
}
export class Obstacle extends UI_obstacleCom {
    data: ObstacleData;
    size: number = 1;
    gap: number = 0;
    followingObstacleCreated = false;
    remove = false;
    collisionBoxes: CollisionBox[];

    sizeMaxM = 1; //   
    speedOffset: number = 0; // 

    scale: number = 0.6;
    MAX_GAP_COEFFICIENT: number = 1.5; //  
    constructFromResource() {
        super.constructFromResource();
    }

    setData(data: ObstacleData) {
        this.setScale(this.scale, this.scale);
        this.m_c1.selectedPage = data.type;
        this.data = data;
        this.size = getRandomIntNum(1, this.sizeMaxM);
        this.collisionBoxes = CollisionBox.cloneCollisionBoxes(this.data.collisionBoxes, this.scale);
        this.drawCollisionBoxes();
        let speed = MainScene.inst.speed;
        // Only allow sizing if we're at the right speed.
        if (this.size > 1 && this.data.multipleSpeed > speed) {
            this.size = 1;
        }
        // this.width = this.typeConfig.width * this.size;
        this.x = RunnerConfig.DEFAULT_WIDTH + this.width;
        // Check if obstacle can be positioned at various heights.
        let tempY: number;
        if (Array.isArray(this.data.yPos)) {
            // var yPosConfig = IS_MOBILE ? this.typeConfig.yPosMobile :
            //     this.typeConfig.yPos;
            tempY = this.data.yPos[getRandomIntNum(0, this.data.yPos.length - 1)];
        } else {
            tempY = this.data.yPos;
        }
        //  123
        this.y = RunnerConfig.DEFAULT_HEIGH / 2 + 100// - tempY - this.height / 2 ;
        if (this.size > 1) {
            this.collisionBoxes[1].width = this.width - this.collisionBoxes[0].width -
                this.collisionBoxes[2].width;
            this.collisionBoxes[2].x = this.width - this.collisionBoxes[2].width;
        }
        // For obstacles that go at a different speed from the horizon.
        if (this.data.speedOffset) {
            this.speedOffset = Math.random() > 0.5 ? this.data.speedOffset :
                -this.data.speedOffset;
        }
        this.gap = this.getGap(RunnerConfig.GAP_COEFFICIENT, speed);
    }

    drawCollisionBoxes() {
        this.collisionBoxes.forEach((coll: CollisionBox) => {
            let sp = new Laya.Sprite();
            this.displayObject.addChild(sp);
            sp.graphics.drawRect(coll.x, coll.y, coll.width / this.scale, coll.height / this.scale, "#f00", "#0f0", 3);
        })
    }
    /**
     * Obstacle frame update.
     * @param {number} deltaTime
     * @param {number} speed
     */
    update(deltaTime) {
        let speed = MainScene.inst.speed;
        if (!this.remove) {
            if (this.data.speedOffset) {
                speed += this.speedOffset;
            }
            this.x -= (speed * MainScene.inst.FPS / 1000) * deltaTime;
            if (!this.isVisible()) {
                if (!this._displayObject.destroyed) {
                    this.dispose();
                }
                this.remove = true;
            }
        }
        else {
            if (!this._displayObject.destroyed) {
                this.dispose();
            }
        }
    }

    getGap(gapCoefficient, speed) {
        var minGap = Math.round(this.data.width * speed +
            this.data.minGap * gapCoefficient);
        var maxGap = Math.round(minGap * this.MAX_GAP_COEFFICIENT);
        return getRandomIntNum(minGap, maxGap);
    }

    isVisible() {
        return this.x + this.width > 0;
    }
}


