/**
 *  C:/A/B/C/D/example.ts
 */
export const path = {

	/**
	 * "C:/A/B//C//D//example.ts"=>"C:/A/B/C/D/example.ts"
	 * @param filename 
	 */
	normalize: (filename: string): string => {
		const arr = filename.split("/");
		return arr.filter((value, index) => !!value || index == arr.length - 1).join("/");
	},

	/**
	 *  lh  ls
	 */
	res3DUrlByName: (sceneName: string, skinName: string, isScene: boolean = true): string => {
		return "assets/unity_exported/LayaScene_" + sceneName + "/Conventional/" + skinName + (isScene ? ".ls" : ".lh");
	},
	/**
	 * "C:/A/B/example.ts"=>"example.ts"
	 * @param filename 
	 * @return 
	 */
	basename: (filename: string): string => {
		return filename.substr(filename.lastIndexOf("/") + 1);
	},

	/**
	 * "C:/A/B/example.ts"=>"C:/A/B"
	 * @param path 
	 * @return 
	 */
	dirname: (path: string): string => {
		return path.substr(0, path.lastIndexOf("/"));
	},

	/**
	 *  " text.txt" => "txt"
	 * @param path 
	 */
	extension: (filename: string): string => {
		let pos = filename.lastIndexOf('.');
		if (pos >= 0 && pos < filename.length - 1) {
			return filename.substring(pos + 1, filename.length);
		}
		return "";
	},

	join(dir: string, ...pathes: string[]) {
		let ret = dir;
		for (const p of pathes) {
			if (p) {
				ret += '/' + p;
			}
		}
		return ret;
	}
}
